body {
  margin: 0;
  font-family: 'Source Sans', 'Courier';
  background-color: rgb(16, 16, 21);
  background-image: url(./img/spaceback.png);
  background-size: 960px;
  background-attachment: fixed;
}

* {
  font-family: 'Source Sans', Courier, monospace;
}

@font-face {
  font-family: 'Source Sans';
  src: local('Source'), url(./fonts/Source_Sans_Pro/SourceSansPro-Light.ttf) format('truetype');
}

@font-face {
  font-family: 'Source Sans Italics';
  src: local('Source'), url(./fonts/Source_Sans_Pro/SourceSansPro-LightItalic.ttf) format('truetype');
}

@font-face {
  font-family: 'Bebas Neue';
  src: local('Source'), url(./fonts/Bebas_Neue/BebasNeue-Regular.ttf) format('truetype');
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgb(64, 64, 64);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 15px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 230, 109, 0.5);
}